import React, { Component } from 'react';
import { Divider, Button } from './';
import { Threads, Tree } from '../interface';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import InfoText from './InfoText';
import LinkButton from './LinkButton';

interface SidebarState {
   [key: string]: boolean | string | Array<any>,
   RemappedTree: Array<{
      section: string,
      threads: Threads
   }>,
   FilteredItems: Array<{section: string, threads: Threads}>,
   inputContent: string
}

class Sidebar extends Component<RouteComponentProps, SidebarState> {
   state: SidebarState = {
      RemappedTree: (() => {
         const Result: Array<{
            section: string,
            threads: Threads
         }> = [];

         Object.keys(Tree).forEach(key => {
            Result.push({
               section: key,
               threads: Tree[key]
            })
         })

         return Result;
      })(),
      FilteredItems: [],
      inputContent: ''
   }

   constructor(props: any) {
      super(props);

      this.handleChange = this.handleChange.bind(this);
   }

   componentDidMount() {
      Object.keys(Tree).forEach(key => {
         this.setState({
            [key]: true,
         });
         
      })
   }

   resetFilteredItems() {
      this.setState({
         FilteredItems: []
      });
   }

   handleChange(event: React.ChangeEvent<HTMLInputElement>) {
      const { target: { value }} = event;

      if (value.trim() === "") {
         return this.resetFilteredItems();
      }

      // find matched section / thread in object.
      const FilteredKeywords = this.state.RemappedTree.map(data => {
         const Keyword = data.threads.filter(item => item.name.toLowerCase().includes(value.toLocaleLowerCase()));

         return {
            section: data.section,
            threads: Keyword
         }
      });

      this.setState({
         FilteredItems: FilteredKeywords
      });
   }

   render() {
      return (
         <div id='lua-sidebar'>
            <h3 style={{cursor: 'pointer'}} onClick={() => this.props.history.replace('/')}>
               Lua.Org Remaster
            </h3>
            <div id='lua-sidebar-body' style={{paddingLeft: '10px', paddingRight: '10px'}}>
               
               <Divider margin Direction='horizontal'/>

               {/* Find Threads */}
               <input type='text' placeholder='Find Thread' className='lua-Inputbase text' onChange={this.handleChange} />
               <Divider Direction='horizontal' />

               {/* search */}
               <div className='lua-serach' >
                  {this.state.FilteredItems.length !== 0 && 
                     this.state.FilteredItems.map((data, index) => {
                        if (data.threads.length < 1) return null;

                        return (
                           <div key={`lua-sidebar-search-${index}`} style={{textAlign: 'center'}} className='lua-Inputbase button' onClick={() => {
                              if (data.threads.length === 1) {
                                 this.props.history.push(data.threads[0].path);
                              }
                           }}>
                              {data.section}
                              <br/>
                              {data.threads.map((thread, index) => {
                                 return <LinkButton key={`lua-sidebar-search-child-${index}`} path={thread.path}><InfoText>{thread.name}</InfoText></LinkButton>
                              })}
                           </div>
                        )
                     }) 
                  }
               </div>


               <ul className='lua-list'>

                  {Object.keys(Tree).map((key, index) => {
                     const TreeData = Tree[key];
                     // Dropdown
                     let TreeItemDiv = (
                        <div className={`lua-list background`} id={`lua-sidebar-list-${index}`}>
                           {TreeData.map((Data, index) => {
                              return <Button key={`lua-treedata-${index}`} onClick={() => {
                                 this.props.history.replace(Data.path || '/');
                              }}>
                                 {Data.name}
                              </Button>
                           })}
                        </div>
                     )

                     let UpdateList = () => {
                        this.setState(current => ({
                           [key]: !current[key],
                        }))
                        const list = document.getElementById(`lua-sidebar-list-${index}`);
                        if (list) {
                           if (!this.state[key]) {
                              list.style.height = '0px';
                              list.style.padding = '0px';
                              list.style.overflow = 'hidden';
                           } else {
                              list.style.height = `${list.scrollHeight}px`;
                              list.style.padding = '7px';
                              list.style.overflow = 'auto';
                           }
                        }
                     }
                     
                     return (
                        <li className={`lua-listitem-container ${!this.state[key] ? 'active' : ''}`} key={`lua-listitem-${index}`}>
                           <Button onClick={() => {
                              if (TreeData.length === 0) return;
                              UpdateList();
                           }}>
                              {key}
                           </Button>
                           {TreeItemDiv}
                        </li>
                     )
                  })}
               </ul>
            </div>
         </div>
      )
   }
}

export default withRouter(Sidebar);