import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';

window.onload = () => {
  // Alert container init  
  const AlertContainer = document.createElement('div');
  AlertContainer.classList.add('lua-alert');
  AlertContainer.id = 'lua-alert-container';

  document.getElementById('root')?.append(AlertContainer);
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);

