import React from "react";
import { createRipples, RipplesProps } from 'react-ripples';

interface ButtonProps extends RipplesProps {
   children?: any;
   duration?: number;
   color?: string;
   id?: string
}

const Button = (
   {children, onClick, duration, color, id}: ButtonProps
) => {
   const Ripple = createRipples({
      color: color || 'rgba(255, 255, 255, 0.5)',
      during: duration || 300,
      className: 'lua-ripple',
   });

   return (
      <Ripple onClick={(event) => {
         onClick && onClick(event);
      }}>
         <button 
            className='lua-button'
            id={id || ''}
         >
         {children}
         </button>
      </Ripple>
   )
}

export default Button;