interface showAlertProps {
   variant: 'default' | 'success' | 'error'
}

const animateCSS = (element: string, animation: string, prefix = 'animate__') =>
  new Promise((resolve, reject) => {
    const animationName = `${prefix}${animation}`;
    const node = document.getElementById(element);

    node?.classList.add(`${prefix}animated`, animationName);

    function handleAnimationEnd(event: any) {
      event.stopPropagation();
      node?.classList.remove(`${prefix}animated`, animationName);
      resolve('Animation ended');
    }

    node?.addEventListener('animationend', handleAnimationEnd, {once: true});
  });

function makeKey(length: number) {
   var result           = '';
   var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
   var charactersLength = characters.length;

   for ( var i = 0; i < length; i++ ) {
     result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }

   return result;
}

function createAlertElement(content: string, key: string): HTMLDivElement {
   const Alert = document.createElement('div');
   const CheckElement = '<svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 1024 1024" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm193.5 301.7l-210.6 292a31.8 31.8 0 0 1-51.7 0L318.5 484.9c-3.8-5.3 0-12.7 6.5-12.7h46.9c10.2 0 19.9 4.9 25.9 13.3l71.2 98.8 157.2-218c6-8.3 15.6-13.3 25.9-13.3H699c6.5 0 10.3 7.4 6.5 12.7z"></path></svg>';

   Alert.innerHTML = CheckElement + `\t${content}`;
   Alert.id = key;
   Alert.classList.add('animate__animated', 'animate__fadeIn');
   Alert.style.setProperty('--animate-duration', '0.3s');

   return Alert;
}

function showAlert(content: string, config?: showAlertProps): string | null {
   const key = makeKey(15);
   const Alert = createAlertElement(content, key);
   
   Alert.classList.add(config?.variant || 'default');

   const AlertContainer = document.querySelector('#lua-alert-container');

   if (AlertContainer) {
      AlertContainer.append(Alert);

      return key;
   }

   Alert.remove();
   return null;
}

async function removeAlert(key: string): Promise<void> {
   const AlertContainer = document.querySelector('#lua-alert-container');

   if (AlertContainer) {
      const Alert = document.getElementById(key);
      if (Alert) {
         await animateCSS(key, 'fadeOut');
         Alert.remove();
      }
   }
}

function timeOutAlert(content: string, timeout: number, config?: showAlertProps): void {
   const key = makeKey(15);
   const Alert = createAlertElement(content, key);
   Alert.classList.add(config?.variant || 'default');

   const AlertContainer = document.querySelector('#lua-alert-container');

   if (AlertContainer) {
      AlertContainer.append(Alert);

      setTimeout(() => {
         removeAlert(key);
      }, timeout)
   }
}

export {
   showAlert,
   removeAlert,
   timeOutAlert
}