// Credit: https://github.com/react-syntax-highlighter/react-syntax-highlighter

import React, { Component } from "react";
import { LightAsync as SyntaxHighlighter} from 'react-syntax-highlighter';
import { darcula } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { Button } from ".";
import { timeOutAlert } from "../library/Alert";
import { IoIosCopy } from 'react-icons/io';

export default class Syntax extends Component<{children?: string, value?: string}> {

   render() {
      return (
         <div>
            <div style={{width: '100px', marginLeft: 'auto'}}>
               <Button onClick={async () => {
                  navigator.clipboard && navigator.clipboard.writeText(this.props.children || this.props.value || ''); timeOutAlert('Copied !', 2000);
               }}>
                  <IoIosCopy size='15' />
               </Button>
            </div>
            <SyntaxHighlighter language="lua" style={darcula} showLineNumbers wrapLines={true} lineProps={{style: { whiteSpace: 'pre'}}}>
               {this.props.children || this.props.value || ''}
            </SyntaxHighlighter>
         </div>
      )
   }
}